<template>
  <div class="page" id="pageDetail">
    <mt-header class="bar-nav" title="充值">
      <mt-button slot="left" icon="back" v-back-link></mt-button>
      <router-link to="/account/cash_record/?type=tab-con1" slot="right">
        <mt-button >记录</mt-button>
      </router-link>
    </mt-header>
    <div class="recharge-top">
      <p>账户余额</p>
      <p>{{ resdata.money | currency('', 2)}}元</p>
    </div>
    <form :action="submitUrl" method="post" id="formId">
      <div class="form-text margin-t-15">
        <label>充值金额(元)</label>
        <input type="number" name="amount" v-model.trim="inputMoney" class="input-text" placeholder="请输入金额">
      </div>
      <input type="hidden" name="rechargeToken" :value="resdata.rechargeToken">
      <input type="hidden" name="__sid" :value="getParams.__sid">
      <input type="hidden" name="userId" :value="getParams.userId">
    </form>
    <div class="confirm-btn">
      <mt-button v-show="!inputMoney" size="large" type="default" disabled>充 值</mt-button>
      <mt-button v-show="inputMoney" size="large" type="danger" @click.native="confirmSub" :disabled="resdata==''" >充 值</mt-button>
    </div>

    <div class="b-tips margin-b-30">
      <i class="main-color">☆</i>点击将跳转到资金托管账户进行充值
    </div>
    <div class="form-prompt">
      <p class="prompt-title">
        温馨提示：
      </p>
      <p v-html="resdata.warmTips"></p>
      <!--<p>1、充值/提现必须为开通网上银行的借记卡，不支持存折、信用卡充值；</p>-->
      <!--<p>2、充值期间，请勿关闭微信，待充值成功并返回后，可在充值记录中查看充值金额；</p>-->
      <!--<p>3、严禁信用卡套现、虚假交易等行为。</p>-->
    </div>
    <router-link to="/account/recharge/support_bank">
      <div class="bank-description-tip secondary-color" >支持银行说明</div>
    </router-link>
  </div>
</template>
<script>
  import * as ajaxUrl from '../../ajax.config'
  export default {
    data(){
      return {
        submitUrl: ajaxUrl.domain + '/app/member/recharge/doRecharge.html',
        resdata: '',
        inputMoney:'',
        getParams: {
          userId: this.$store.state.user.userId,
          __sid: this.$store.state.user.__sid
        }
      };
    },
    created(){
      this.$indicator.open({spinnerType: 'fading-circle'}) //提示初始化加载
      this.$http.get(ajaxUrl.toRecharge, {params: this.getParams}).then((res) => {
        this.resdata = res.data.resData
        this.$indicator.close()
      })
    },
    watch: {
      inputMoney (newV, oldV) {
        if(newV){
          if(newV < this.resdata.rechargeMinAmount){ //
            this.$toast({
              message: '充值金额不能小于' + this.resdata.rechargeMinAmount +'元',
              position: 'top'
            })
            this.inputMoney = this.resdata.rechargeMinAmount
          }
          if(newV > this.resdata.rechargeMaxAmount){
            this.$toast({
              message: '充值金额不能大于' + this.resdata.rechargeMaxAmount +'元',
              position: 'top'
            })
            this.inputMoney = oldV
          }
        }
      }
    },
    methods: {
      confirmSub(){
          document.getElementById('formId').submit()
          if(this.$route.query.from == 'investBid'){
            sessionStorage.from_investBid_id =this.$route.query.id
          }else if(this.$route.query.from == 'bondBid'){
            sessionStorage.from_bondBid_id =this.$route.query.id
          }
      }
    }
  }
</script>
<style scoped>
  .recharge-top{
    height: .8rem;
    width: 100%;
    background: #F95A28;
    padding: 0 5%;
  }
  .recharge-top p{
    margin-top: .16rem;
    float: left;
    width: 100%;
    line-height: 1;
    font-size: .16rem;
    font-family: arial;
    color: #fff;
  }
  .confirm-btn{
    margin: .3rem 5% 0;
  }
  .b-tips{
    font-size: .12rem;
    color: #999;
    margin-top: .18rem;
    text-align: center;
  }
  .bank-description-tip{
    display: inline-block;
    padding: 0 5%;
    font-size: .12rem;
    line-height: .42rem;
  }
</style>



